#--serviceIcons button,
#--serviceSpriteIcons button {
  border: 0;
  border-radius: 0;
  box-shadow: none;
  background: none;
  padding: 0;
  margin: 0;
  outline: none;
  text-decoration: none;
  cursor: pointer;
  text-align: center;
  -webkit-appearance: none;
  -moz-appearance: none;
}

@keyframes blur-a {
  from {
    text-shadow: 0px 0px 10px #fff, 0px 0px 10px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 50px #fff, 0px 0px 50px #fff, 0px 0px 50px #7B96B8, 0px 0px 150px #7B96B8, 0px 10px 100px #7B96B8, 0px 10px 100px #7B96B8, 0px 10px 100px #7B96B8, 0px 10px 100px #7B96B8, 0px -10px 100px #7B96B8, 0px -10px 100px #7B96B8;
  }
}
:root {
  --h1Size: 6rem;
  --h2Size: 4.8rem;
  --h3Size: 3.6rem;
  --h4Size: 2.8rem;
  --h5Size: 2.1rem;
  --h6Size: 1.8rem;
  --color-text: rgb(41, 38, 43);
  --color-text-rgb: 41, 38, 43;
  --color-text-r: 41;
  --color-text-g: 38;
  --color-text-b: 43;
  --color-main: rgb(64, 28, 92);
  --color-main-rgb: 64, 28, 92;
  --color-main-r: 64;
  --color-main-g: 28;
  --color-main-b: 92;
  --color-error: rgb(251, 92, 65);
  --color-error-rgb: 251, 92, 65;
  --color-error-r: 251;
  --color-error-g: 92;
  --color-error-b: 65;
  --color-accent: #FEBE09;
  --color-accent_light: #FFD150;
  --color-bg: #ffffff;
  --color-bg_dark: #ECE8EF;
  --color-border_light: #EBE7EE;
  --color-border_table-head: #DAD6DE;
  --color-border: #A59EA9;
  --color-border_dark: #7F7783;
  --table-border: #e0d9e4;
  --color-main_light: #6A4B82;
  --color-link-blue: #347FF0;
  --color-link-green: #13B737;
}
@media (max-width: 1023px) {
  :root {
    --h1Size: clamp(4rem, 9vw, 6rem);
    --h2Size: clamp(3.7rem, 6.5vw, 4.8rem);
    --h3Size: clamp(3.2rem, 6vw, 3.6rem);
    --h4Size: clamp(2.2rem, 4.7vw, 2.8rem);
    --h5Size: clamp(1.6rem, 4.3vw, 2.1rem);
    --h6Size: clamp(1.6rem, 4vw, 1.8rem);
  }
}
@media print {
  :root {
    --color-text: #000000;
    --color-bg: #ffffff;
  }
}

/*system from figma*/
:root {
  --grey-10: var(--color-border_light);
  --grey-30: var(--color-border);
  --text: var(--color-text);
}

#--serviceInfo .info-tool {
  position: fixed;
  top: 2vmin;
  right: 2vmin;
  width: 12em;
  min-height: 3em;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5em;
  opacity: 0;
  transform: translateY(0.6em);
  z-index: -1;
  color: var(--color-text);
  pointer-events: none;
  font-size: 1.5rem;
  border-radius: 10px;
  filter: blur(10px);
}
#--serviceInfo .info-tool p {
  z-index: 1;
  position: relative;
}
#--serviceInfo .info-tool::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}
@media only screen and (max-width: 767px) {
  #--serviceInfo .info-tool {
    width: calc(100% - 4vmin);
  }
}
#--serviceInfo .info-tool_success::before {
  background: green;
}
#--serviceInfo .info-tool_error::before {
  background: red;
}
#--serviceInfo .info-tool.start-animation {
  z-index: 999;
  transition: all 0.33s cubic-bezier(0.23, 1, 0.32, 1);
}
#--serviceInfo .info-tool.start-animation.is-animating {
  filter: blur(0);
  opacity: 1;
  transform: none;
}

#--serviceIcons .-service-icons,
#--serviceSpriteIcons .-service-icons {
  border: 1px solid var(--color-border);
  border-radius: 5px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  padding: 1em 0.5em 0.5em;
  cursor: pointer;
  text-align: center;
  position: relative;
  user-select: none;
}
#--serviceIcons .-service-icons::after,
#--serviceSpriteIcons .-service-icons::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: 0.4s ease;
}
#--serviceIcons .-service-icons:not(.active),
#--serviceSpriteIcons .-service-icons:not(.active) {
  pointer-events: none;
  filter: grayscale(100%) opacity(0.4);
}
#--serviceIcons .-service-icons:hover::after,
#--serviceSpriteIcons .-service-icons:hover::after {
  box-shadow: 0px 5px 25px rgba(var(--color-bg-invert-r), var(--color-bg-invert-g), var(--color-bg-invert-b), 0.2);
}
#--serviceIcons .-service-icons .img-wrap,
#--serviceSpriteIcons .-service-icons .img-wrap {
  margin: 0;
  max-width: 60%;
  flex: 1 1 100%;
  width: 100%;
  aspect-ratio: 1/1;
  display: flex;
  align-items: center;
  justify-content: center;
}
#--serviceIcons .-service-icons img,
#--serviceSpriteIcons .-service-icons img {
  margin: 0;
}
#--serviceIcons .-service-icons .text-wrap,
#--serviceSpriteIcons .-service-icons .text-wrap {
  display: flex;
  overflow: hidden;
  user-select: none;
  gap: var(--gap);
  mask-image: linear-gradient(var(--mask-direction, to right), rgba(0, 0, 0, 0), black 20%, black 80%, rgba(0, 0, 0, 0));
  position: static;
}
#--serviceIcons .-service-icons .text-wrap::after,
#--serviceSpriteIcons .-service-icons .text-wrap::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: 0.4s ease;
}
#--serviceIcons .-service-icons .text,
#--serviceSpriteIcons .-service-icons .text {
  --size: 50px;
  --gap: calc(var(--size) / 3);
  --scroll-start: 0;
  --scroll-end: calc(-90% - var(--gap));
  --duration: 25s;
  font-size: 80%;
  line-height: 1.3;
  height: 2em;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: var(--gap);
  min-width: 100%;
  animation: scroll-x var(--duration) var(--anim-delay, 0) linear infinite;
}
#--serviceIcons .-service-icons .text span,
#--serviceSpriteIcons .-service-icons .text span {
  white-space: nowrap;
}

@keyframes scroll-x {
  from {
    transform: translateX(var(--scroll-start));
  }
  to {
    transform: translateX(var(--scroll-end));
  }
}
#--serviceFilterBtns,
#--servicesSearch {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5em;
}
#--serviceFilterBtns .btn:not(.active),
#--servicesSearch .btn:not(.active) {
  opacity: 0.4;
}
#--serviceFilterBtns .btn,
#--servicesSearch .btn {
  font-size: 70%;
  text-transform: none;
  min-height: auto;
  min-width: fit-content;
}
#--serviceFilterBtns .btn span,
#--servicesSearch .btn span {
  text-transform: none;
}
#--serviceFilterBtns .btn span,
#--servicesSearch .btn span {
  pointer-events: none;
}