@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@300;400;500;600;800&display=swap");
body {
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

.section_intro .section__bg, .section_intro::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: inherit;
}

.section_intro .section__bg, .section_intro::after {
  width: 100%;
  height: 100%;
}

.custom-tags .tag .text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.review-table button, .calc-data button, .toTopBtn, .from-to button, .review-table__pagination button, .btn {
  border: 0;
  border-radius: 0;
  box-shadow: none;
  background: none;
  padding: 0;
  margin: 0;
  outline: none;
  text-decoration: none;
  cursor: pointer;
  text-align: center;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.layout__main ol:not(.breadcrumbs), .simple-compare__list, .check-title-list, .list-withIcon, .mainpage-iconedList, .contents__list, .fingertips__list ul, .line-icons, .footer ul, .menu-item .sub-menu, .nav li, .clear-list {
  padding: 0;
  margin: 0;
  list-style: none;
}

@keyframes blur-a {
  from {
    text-shadow: 0px 0px 10px #fff, 0px 0px 10px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 25px #fff, 0px 0px 50px #fff, 0px 0px 50px #fff, 0px 0px 50px #7B96B8, 0px 0px 150px #7B96B8, 0px 10px 100px #7B96B8, 0px 10px 100px #7B96B8, 0px 10px 100px #7B96B8, 0px 10px 100px #7B96B8, 0px -10px 100px #7B96B8, 0px -10px 100px #7B96B8;
  }
}
.post-item__description {
  padding: 0;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: var(--trunk, 5);
  -webkit-box-orient: vertical;
}

:root {
  --h1Size: 6rem;
  --h2Size: 4.8rem;
  --h3Size: 3.6rem;
  --h4Size: 2.8rem;
  --h5Size: 2.1rem;
  --h6Size: 1.8rem;
  --color-text: rgb(41, 38, 43);
  --color-text-rgb: 41, 38, 43;
  --color-text-r: 41;
  --color-text-g: 38;
  --color-text-b: 43;
  --color-main: rgb(64, 28, 92);
  --color-main-rgb: 64, 28, 92;
  --color-main-r: 64;
  --color-main-g: 28;
  --color-main-b: 92;
  --color-error: rgb(251, 92, 65);
  --color-error-rgb: 251, 92, 65;
  --color-error-r: 251;
  --color-error-g: 92;
  --color-error-b: 65;
  --color-accent: #FEBE09;
  --color-accent_light: #FFD150;
  --color-bg: #ffffff;
  --color-bg_dark: #ECE8EF;
  --color-border_light: #EBE7EE;
  --color-border_table-head: #DAD6DE;
  --color-border: #A59EA9;
  --color-border_dark: #7F7783;
  --table-border: #e0d9e4;
  --color-main_light: #6A4B82;
  --color-link-blue: #347FF0;
  --color-link-green: #13B737;
}
@media (max-width: 1023px) {
  :root {
    --h1Size: clamp(4rem, 9vw, 6rem);
    --h2Size: clamp(3.7rem, 6.5vw, 4.8rem);
    --h3Size: clamp(3.2rem, 6vw, 3.6rem);
    --h4Size: clamp(2.2rem, 4.7vw, 2.8rem);
    --h5Size: clamp(1.6rem, 4.3vw, 2.1rem);
    --h6Size: clamp(1.6rem, 4vw, 1.8rem);
  }
}
@media print {
  :root {
    --color-text: #000000;
    --color-bg: #ffffff;
  }
}

/*system from figma*/
:root {
  --grey-10: var(--color-border_light);
  --grey-30: var(--color-border);
  --text: var(--color-text);
}

