.header-trunk-line_2 {
  padding: 0;
  height: 2.5em;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.hidden {
  display: none !important;
}

.svg-icon,
img {
  width: 100%;
}

.bordered {
  border: 1px solid var(--color-border);
  border-radius: 5px;
}

.g-columns-1-2-4 {
  --bs-columns: 1;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .g-columns-1-2-4 {
    --bs-columns: 2;
  }
}
@media only screen and (min-width: 1024px) {
  .g-columns-1-2-4 {
    --bs-columns: 4;
  }
}

.iconWithTooltip {
  display: inline-flex;
}

.section_big-gap {
  --innerGap: 8rem;
  --innerGap: calc(5rem + (8 - 5) * ((100vw - 32rem) / (192 - 32)));
  padding-top: var(--innerGap);
  padding-bottom: var(--innerGap);
}
@media (max-width: 32rem) {
  .section_big-gap {
    --innerGap: 5rem;
  }
}
@media (min-width: 192rem) {
  .section_big-gap {
    --innerGap: 8rem;
  }
}
.section_medium-gap {
  --innerGap: 6.5rem;
  --innerGap: calc(3rem + (6.5 - 3) * ((100vw - 32rem) / (192 - 32)));
  padding-top: var(--innerGap);
  padding-bottom: var(--innerGap);
}
@media (max-width: 32rem) {
  .section_medium-gap {
    --innerGap: 3rem;
  }
}
@media (min-width: 192rem) {
  .section_medium-gap {
    --innerGap: 6.5rem;
  }
}
.section_white {
  background-color: var(--color-bg);
}
.section_light {
  background-color: var(--color-bg_dark);
}
.section_dark {
  background-color: var(--color-main);
  color: var(--color-bg);
}
.section_dark a:not(.btn):not(.blue-link):not(.green-link) {
  color: var(--color-link-blue);
}
.section_dark a:not(.btn):not(.blue-link):not(.green-link):hover {
  text-decoration: none;
}
.section_intro {
  --overlay: 14;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 700px;
  margin-top: -20px;
  color: var(--color-bg);
}
.section_intro::after {
  content: "";
  background-color: black;
  opacity: calc(var(--overlay) * 0.01);
  z-index: 0;
}
.section_intro .section__bg {
  object-fit: cover;
  z-index: -1;
}
.section_intro > .container {
  position: relative;
  z-index: 1;
}
@media only screen and (min-width: 1600px) {
  .section_intro > .container {
    max-width: 115rem;
  }
}
.section_intro > .container .btn {
  margin-top: 2rem;
}

.section_animation::after {
  content: "";
  background-color: #000;
  opacity: calc(var(--overlay) * 0.01);
  z-index: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

[class*=section_animation] {
  position: relative;
  transform-origin: center;
  overflow: hidden;
}

.clearfix .elem-icon {
  padding-left: 0;
}
.clearfix .elem-icon::before {
  display: inline-flex;
  transform: translateY(0.3em);
  margin-right: 0.5em;
  position: relative;
}
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

@media only screen and (min-width: 1024px) {
  .float-right {
    float: right;
    margin-left: 2em;
  }
}
.float-right_6-14 {
  max-width: 100%;
}
@media only screen and (min-width: 1024px) {
  .float-right_6-14 {
    max-width: 45%;
  }
}

@media only screen and (min-width: 1024px) {
  .float-left {
    float: left;
    margin-right: 2em;
  }
}
.float-left_6-14 {
  max-width: 100%;
}
@media only screen and (min-width: 1024px) {
  .float-left_6-14 {
    max-width: 45%;
  }
}

.is-inactive {
  opacity: 0.3;
  filter: grayscale(1);
}

.gold-link {
  color: var(--color-link-blue);
  text-decoration: underline;
  text-decoration-thickness: from-font !important;
  -ms-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto;
}
.gold-link:hover {
  text-decoration: none;
}

.canBreakLink {
  word-break: break-all;
}

main a:not(.btn):not(.link-reset) {
  text-decoration: underline;
}
main a:not(.btn):not(.link-reset):hover {
  text-decoration: none;
}

/*fixes in live*/
@media only screen and (min-width: 768px) {
  #menu-item-17 .sub-menu {
    justify-content: flex-end;
  }
}

.hover-copy {
  position: fixed;
  top: 100%;
  left: 100%;
  width: 780px;
  background: white;
  transform-origin: 0 0;
  box-shadow: 0 0 13px 2px rgba(64, 28, 92, 0.2);
  padding: 1em;
  pointer-events: none;
  opacity: 0;
  transform: scale(0);
  transition: opacity 0.23s ease-in-out;
}
.hover-copy--active {
  opacity: 1;
  transform: scale(0.6);
}